@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;400&display=swap');

.center {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 900px;
    background-color: rgb(197, 188, 177);
    border-radius: 20px;
    margin: 30px 250px;
}

p,
h3 {
    font-family: 'Nunito';
}

.ii {
    text-align: center;
}

.left11 {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-left: 50px;
    font-weight: 700;
    gap: 30px;
}

.right11 {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-right: 50px;
    font-weight: 500;
    gap: 30px;
}

