@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;400&display=swap');

.su {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
}

.card {
    width: 800px;
    height: 300px;
    border: none;
    background: linear-gradient(180deg, rgba(86, 190, 235, 0.55) 0%, rgba(200, 229, 222, 0.64) 99.98%, rgba(213, 209, 213, 0.0328854) 99.99%);
    border-radius: 30px;
}

.inner {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.left {
    display: flex;
    justify-content: center;
    align-items: center;
}

.bbtnn {
    display: flex;
    align-items: center;
    justify-content: center;
}

.bookbtnn {
    width: 80px;
    height: 25px;
    background: #1A3C65;
    border-radius: 60px;
    color: white;
    text-align: center;
    font-family: 'Nunito', sans-serif;
    text-decoration: none;
    font-weight: 500;
}

.card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.c {
    text-align: center;
    font-family: 'Nunito', sans-serif;
    text-decoration: none;
    font-weight: 700;
    font-size: 17px;
    font-style: normal;
    line-height: 20px;
}

.hallna {
    font-family: 'Nunito', sans-serif;
    font-weight: 800;
    font-size: 25px;
    font-style: normal;
    text-transform: uppercase;
}