@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;400&display=swap');

.navbar {
    width: 100vw;
    height: 120px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    background: #FFFFFF;
    padding: 0 49px;
}

.navbar .nav-logo {
    display: flex;
    align-items: center;
   
}

.navbar .nav-logo h3 {
    height: 30px;
    font-family: 'Nunito', sans-serif;
    font-weight: 400;

}

.navbar .navbar-menu {

    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    padding: 6px 0px;
    gap: 26px;


}

.navbar-item1,
.navbar-item2 {

    font-family: 'Nunito', sans-serif;
    text-decoration: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
}

.navbar-item3 {
    padding: 8px 12px;
    width: 122px;
    height: 35px;
    background: #7B2CBF;
    border-radius: 10px;
    text-decoration:none ;

}

span {
    display: flex;
    justify-content: center;
    font-family: 'Nunito', sans-serif;
    font-weight: 400;
    /* font-size: 16px; */
    line-height: 19px;
    color: #FFFFFF;
}
.navbar .toggle-icon{
    display: none
  }
