     @media(max-width: 768px){
        .navbar{
          position: relative;
          transition: 0.5s all ease-in-out;
        }
      
        .navbar.expanded{
          height: 100vh;
        }
      
        .navbar.expanded .navbar-menu{
          position: absolute;
          top: 120px;
          left: 0;
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          opacity: 1;
        }
      
        .navbar.expanded .navbar-menu Link{
          margin: 15px 0;
          font-size: 24px;
        }
        
      
        .navbar .toggle-icon{
          display: block;
          color: #D9D2C3;
          position: absolute;
          top: 28px;
          right: 20px;
        }

        .navbar .nav-logo {
            display: flex;
            align-items: center;
            position: absolute;
            margin-top: 17px;
            top: 0;
        }
      
        .navbar .navbar-menu{
          position: absolute;
          top: -300px;
          left: 0;
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          opacity: 0;
          transition: all 0.5s ease-in-out;
        }
      
        .navbar .navbar-menu Link {
          margin: 15px 0;
          font-size: 24px;
        }
      }