@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;400&display=swap');

.ss{
    display: flex;
    justify-content: center;
    font-family: 'Nunito';
    margin-top: 20px;

}

.parent {
    display: flex;
    align-items: center;
    height: 680px;
    margin: 0 350px;
    flex-direction: column;
    font-family: 'Nunito';
    font-weight: 600;
    background-color: #ffffff;
    width: 800px;
    border-radius: 90px;
}

.pppp{
    margin-top: 40px;
}
button {
    background-color: rgb(49, 176, 215);
    border-radius: 100px;
    box-shadow: rgba(44, 187, 99, .2) 0 -25px 18px -14px inset, rgba(44, 187, 99, .15) 0 1px 2px, rgba(44, 187, 99, .15) 0 2px 4px, rgba(44, 187, 99, .15) 0 4px 8px, rgba(44, 187, 99, .15) 0 8px 16px, rgba(44, 187, 99, .15) 0 16px 32px;
    color: rgb(253, 253, 253);

    cursor: pointer;
    display: inline-block;
    font-family: CerebriSans-Regular, -apple-system, system-ui, Roboto, sans-serif;
    padding: 7px 20px;
    text-align: center;
    text-decoration: none;
    transition: all 250ms;
    border: 0;
    font-size: 16px;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

button:hover {
    box-shadow: rgba(174, 22, 5, 0.35) 0 -25px 18px -14px inset, rgba(232, 12, 12, 0.995) 0 1px 2px, rgba(238, 74, 74, 0.922) 0 2px 4px, rgba(44, 187, 99, .25) 0 4px 8px, rgba(44, 187, 99, .25) 0 8px 16px, rgba(44, 187, 99, .25) 0 16px 32px;
    transform: scale(1.05) rotate(-1deg);
}

